import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      component: () => import('@/views/dashboard/Index'),
      children: [
        // Dashboard
        {
          name: 'Dashboard',
          path: '',
          component: () => import('@/views/dashboard/Dashboard'),
        },
        // Pages
        {
          name: 'User Profile',
          path: 'pages/user',
          component: () => import('@/views/dashboard/pages/UserProfile'),
        },
        {
          name: 'Notifications',
          path: 'components/notifications',
          component: () => import('@/views/dashboard/component/Notifications'),
        },
        {
          name: 'Icons',
          path: 'components/icons',
          component: () => import('@/views/dashboard/component/Icons'),
        },
        {
          name: 'Typography',
          path: 'components/typography',
          component: () => import('@/views/dashboard/component/Typography'),
        },
        // Tables
        {
          name: 'Regular Tables',
          path: 'tables/regular-tables',
          component: () => import('@/views/dashboard/tables/RegularTables'),
        },
        // Maps
        {
          name: 'Google Maps',
          path: 'maps/google-maps',
          component: () => import('@/views/dashboard/maps/GoogleMaps'),
        },
        // Upgrade
        {
          name: 'Upgrade',
          path: 'upgrade',
          component: () => import('@/views/dashboard/Upgrade'),
        },
        // UnitDashboard
        {
          name: 'Unit Dashboard',
          path: 'unit/unit-dashboard',
          component: () => import('@/views/unit/UnitDashboard'),
        },
        // UnitMyWork
        {
          name: 'Unit Dashboard',
          path: 'unit/unit-card-work',
          component: () => import('@/views/unit/UnitCardMyWork'),
        },
         // UnitMyWork
        {
          name: 'Unit Search',
          path: 'unit/unit-list-search',
          component: () => import('@/views/unit/UnitListSearch'),
        },
        // ready
        {
          name: 'Ready',
          path: 'ready',
          component: () => import('@/views/Ready'),
        },
        // ready
        {
          name: 'Ready',
          path: 'ready2',
          component: () => import('@/views/Ready'),
        },
        // ready
        {
          name: 'Ready',
          path: 'ready3',
          component: () => import('@/views/Ready'),
        },
      ],
    },
  ],
})
